                        <v-checkbox v-model="user.dependente.tipo8" label="Irmo (a), neto (a) ou bisneto (a) sem arrimo dos pais, do (a) qual detenha a guarda judicial, em qualquer idade, quando incapacitado fsica e/ou mentalmente para o trabalho;" value="1"></v-checkbox>
