<script>
import userService from "../services/userService";

export default {
    data: () => ({
        valid: true,
        name: '',
        menu: false,
        menu_rg: false,
        date: null,
        id_gerente: null,
        date1: null,
        user: {
            complemento_trabalhador: {},
            dependente: {
                tipo1: 0,
                tipo2: 0,
                tipo3: 0,
                tipo4: 0,
                tipo5: 0,
                tipo6: 0,
                tipo7: 0,
                tipo8: 0,
                tipo9: 0,
                tipo10: 0,
                tipo11: 0,
            },
            identificacao: {},
            contato: {
                telefone_fixo:'',
                celular: '',
                email_institucional: ''
            },
            contexto: [],
        },
        nameRules: [
            v => !!v || 'Requerido',
        ],
        email: '',
        regime_de_bens: '',
        contexto: ["contabil", "fisica", "pessoal"],
        regime: ['Comunho parcial de bens', 'Comunho universal de bens', 'Separao total de bens', 'Separao obrigatria de bens', 'Participao final nos aquestos'],
        emailRules: [
            v => !!v || 'E-mail is required',
            v => /.+@.+/.test(v) || 'E-mail must be valid'
        ],
        select: null,
        items: [{
                name: 'ANALFABETO',
                id: 1
            },
            {
                name: 'AT A 4 SRIE INCOMPLETA DO ENSINO FUNDAMENTAL (ANTIGO 1 GRAU OU PRIMRIO), OU QUE TENHA SE ALFABETIZADO SEM TER FREQUENTADO ESCOLA REGULAR',
                id: 2
            },
            {
                name: '4 SRIE COMPLETA DO ENSINO FUNDAMENTAL (ANTIGO 1 GRAU OU GINSIO)',
                id: 3
            },
            {
                name: 'DA 5 A 8 SERIE DO ENSINO FUNDAMENTAL (ANTIGO 1 GRAU OU GINSIO)',
                id: 4
            },
            {
                name: 'ENSINO FUNDAMENTAL COMPLETO (ANTIGO 1 GRAU, PRIMRIO OU GINSIO)',
                id: 5
            },
            {
                name: 'ENSINO MDIO INCOMPLETO (ANTIGO 2 GRAU, SECUNDRIO OU COLEGIAL)',
                id: 6
            },
            {
                name: 'ENSINO MDIO COMPLETO (ANTIGO 2 GRAU, SECUNDRIO OU COLEGIAL)',
                id: 7
            },
            {
                name: 'EDUCAO SUPERIOR INCOMPLETA',
                id: 8
            },
            {
                name: 'EDUCAO SUPERIOR COMPLETA',
                id: 9
            },
            {
                name: 'PS-GRADUAO',
                id: 10
            },
            {
                name: 'MESTRADO',
                id: 11
            },
            {
                name: 'DOUTORADO',
                id: 12
            }
        ],
        checkbox: false
    }),
    async created() {
        this.id_gerente = await this.$route.params.id;
    },
    computed: {
        computedDateFormatted() {
            //this.this.user.data_nascimento = this.formatDate(this.date);
            return this.formatDate(this.user.data_nascimento);
        }
    },
    watch: {
        menu(val) {
            val && this.$nextTick(() => (this.$refs.picker.activePicker = 'YEAR'));
            this.user.data_nascimento = this.formatDate(this.date);
        },
        menu_rg(val) {
            val && this.$nextTick(() => (this.$refs.picker.activePicker = 'YEAR'));
            this.user.identificacao.data_expedicao = this.formatDate(this.date1);
        },
        date(val) {
            this.user.data_nascimento = this.formatDate(this.date)
        },
        date1(val) {
            this.user.identificacao.data_expedicao = this.formatDate(this.date1)
        }
    },

    methods: {
        onChange(data) {
            switch (data) {
                case 'contabil':
                        this.user.contexto[0] = 'contabil',
                        this.user.contexto[1] = null,
                        this.user.contexto[2] = null
                    break
                case 'fisica':
                        this.user.contexto[0] = null
                        this.user.contexto[1] = 'fisica',
                        this.user.contexto[2] = null
                    break
                case 'pessoal':
                        this.user.contexto[0] = null,
                        this.user.contexto[1] = null,
                        this.user.contexto[2] = 'pessoal'
                    break
            }
        },
        formatDate(date) {
            if (!date) return null
            const [year, month, day] = date.split('-')
            return `${day}/${month}/${year}`
        },
        parseDate(date) {
            if (!date) return null
            const [month, day, year] = date.split('/')
            return `${day.padStart(2, '0')}-${month.padStart(2, '0')}-${year}}`
        },
        save(date) {
            this.$refs.menu.save(date)
        },
        save2(date) {
            this.$refs.menu_rg.save(date)
        },
        submit() {
            //  console.log(this.user);

            userService
                .cadastro(this.id_gerente, this.user).then(r => {
                    if (r.status == 201) {
                        this.$router.push("/gerente/" + this.id_gerente);
                    }
                });
        },
        clear() {
            this.$refs.form.reset()
            this.$router.push("/gerente");
        }
    }
}
</script>
